<template>
  <div class="search">
    <div class="mapping">
      <el-select
        class="select"
        v-model="colName"
        placeholder="请选择匹配的字段类型"
      >
        <el-option
          v-for="item in colsList"
          :key="item.col"
          :label="item.col"
          :value="item.name"
        >
        </el-option>
      </el-select>
      <el-input
        class="ipt"
        v-model="colValue"
        placeholder="请输入要匹配的字段值"
      ></el-input>
      <el-button type="primary" @click="search">匹配</el-button>
    </div>
    <div class="dataShow">
      <el-table
        :data="tableData"
        ref="table"
        v-loading="loading"
        height="700"
        max-height="calc(100% - 50px)"
        min-height="calc(100% - 50px)"
        border
        stripe
        style="width: 100%"
        :header-cell-style="{ background: '#a6c3e2', color: '#fff' }"
      >
        <el-table-column
          prop="id"
          label="序号"
          min-width="80"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="identifier"
          label="编号"
          min-width="120"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="title"
          label="类别"
          min-width="120"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="englishName"
          label="英文名称"
          min-width="200"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="chineseName"
          label="中文名称"
          min-width="150"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="description"
          label="字段描述"
          min-width="250"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="unit"
          label="单位"
          min-width="80"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="typePrecision"
          label="类型与精度"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="rangeConstraint"
          label="值域/约束"
          min-width="220"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="referenceVariable"
          label="引用变量"
          min-width="100"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="typeCode"
          label="类别码"
          min-width="80"
          show-overflow-tooltip
        >
        </el-table-column>
      </el-table>
      <div class="block">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      colsList: [],
      colName: "",
      colValue: "",
      tableData: [],
      loading: false,
      currentPage: 1,
      pageSize: 200,
      total: 200,
    };
  },
  components: {},
  computed: {},
  created() {
    this.getColsList();
  },
  methods: {
    // 获取字段类型列表
    getColsList() {
      this.$axios({
        url: "/pipeline_data_sys/attribute/cols",
      })
        .then((res) => {
          this.colsList = res.data;
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
    // 搜索
    search() {
      if (this.colName == "") {
        this.$message({
          type: "error",
          message: "请选择要匹配的字段类型",
          duration: 3000,
          showClose: true,
        });
      } else if (this.colValue == "") {
        this.$message({
          type: "error",
          message: "请输入要匹配的字段值",
          duration: 3000,
          showClose: true,
        });
      } else {
        this.loading = true;
        this.$nextTick(() => {
          this.$refs.table.bodyWrapper.scrollTop = 0;
        });
        this.$axios({
          url: `/pipeline_data_sys/attribute/${this.colName}/search/${this.colValue}`,
          params: {
            pageSize: this.pageSize / 10,
          },
        })
          .then((res) => {
            this.tableData = [];
            if (res.data.length != 0) {
              this.total = res.data[0].total * 10;
              res.data.forEach((element) => {
                element.attributes.forEach((item) => {
                  item.title = element.title;
                  item.identifier = element.identifier;
                });
                this.tableData = this.tableData.concat(element.attributes);
              });
            } else {
              this.$message({
                type: "info",
                message: "未找到匹配数据",
                duration: 3000,
                showClose: true,
              });
            }
          })
          .catch((err) => {
            this.$message({
              type: "error",
              message: "网络出错了",
              duration: 3000,
              showClose: true,
            });
          })
          .finally(() => {
            setTimeout(() => {
              this.loading = false;
            }, 400);
          });
      }
    },
    handleCurrentChange(val) {
      this.loading = true;
      this.$nextTick(() => {
        this.$refs.table.bodyWrapper.scrollTop = 0;
      });
      this.$axios({
        url: `/pipeline_data_sys/attribute/${this.colName}/search/${this.colValue}`,
        params: {
          pageNum: val,
          pageSize: this.pageSize / 10,
        },
      })
        .then((res) => {
          this.tableData = [];
          res.data.forEach((element) => {
            element.attributes.forEach((item) => {
              item.title = element.title;
              item.identifier = element.identifier;
            });
            this.tableData = this.tableData.concat(element.attributes);
          });
          setTimeout(() => {
            this.loading = false;
          }, 400);
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
  },
};
</script>

<style lang='scss' scoped>
.search {
  box-sizing: border-box;
  height: calc(100% - 60px);
  padding: 10px 20px;
  .mapping {
    display: flex;
    justify-content: start;
    align-items: center;
    height: 70px;
    .select {
      width: 200px;
      margin-right: 20px;
    }
    .ipt {
      width: 300px;
      margin-right: 20px;
    }
  }
  .dataShow {
    position: relative;
    height: calc(100% - 70px);
    /deep/ .el-table__header {
      height: 80px;
      background-color: #e3e5e9;
    }
    .block {
      position: absolute;
      right: 20px;
      bottom: 10px;
    }
  }
}
/deep/ .el-table .el-table__cell {
  padding: 5px 3px;
}
/deep/ .el-table__body-wrapper::-webkit-scrollbar {
  width: 10px; /*滚动条宽度*/
  height: 10px; /*滚动条高度*/
}
</style>