<template>
  <div class="home">
    <el-container style="height: 100%">
      <el-aside class="aside" width="300px">
        <div class="search">
          <el-input
            class="ipt"
            v-model="categoryName"
            placeholder="请输入类别名"
          ></el-input>
          <el-button
            class="btn"
            type="primary"
            icon="el-icon-search"
            @click="search"
            >搜索</el-button
          >
        </div>
        <div class="menu">
          <el-menu
            @select="getSelect"
            :unique-opened="true"
            :default-active="categoryId"
            :active-text-color="'#409eff'"
          >
            <el-submenu
              :index="String(one.category.id)"
              v-for="one in categoryList"
              :key="one.category.id"
            >
              <template slot="title">{{
                one.category.identifier + "_" + one.category.title
              }}</template>
              <template v-for="two in one.sonCategory">
                <el-submenu
                  v-if="two.sonCategory != null && two.sonCategory.length > 0"
                  :index="String(two.category.id)"
                  :key="two.category.id"
                >
                  <template slot="title">{{
                    two.category.identifier + "_" + two.category.title
                  }}</template>
                  <template v-for="three in two.sonCategory">
                    <el-submenu
                      :index="String(three.category.id)"
                      :key="three.category.id"
                      v-if="
                        three.sonCategory != null &&
                        three.sonCategory.length > 0
                      "
                    >
                      <template slot="title">{{
                        three.category.identifier + "_" + three.category.title
                      }}</template>
                      <el-menu-item
                        :index="String(four.category.id)"
                        v-for="four in three.sonCategory"
                        :key="four.category.id"
                        >{{
                          four.category.identifier + "_" + four.category.title
                        }}</el-menu-item
                      >
                    </el-submenu>
                    <el-menu-item
                      v-else
                      :index="String(three.category.id)"
                      :key="three.category.id"
                    >
                      <template slot="title">{{
                        three.category.identifier + "_" + three.category.title
                      }}</template>
                    </el-menu-item>
                  </template>
                </el-submenu>
                <el-menu-item
                  v-else
                  :index="String(two.category.id)"
                  :key="two.category.id"
                >
                  <template slot="title">{{
                    two.category.identifier + "_" + two.category.title
                  }}</template>
                </el-menu-item>
              </template>
            </el-submenu>
          </el-menu>
        </div>
        <div class="mapping">
          <router-link to="/search">匹配</router-link>
        </div>
      </el-aside>
      <el-main>
        <div class="tableHeader">
          {{ tabName || "表名" }}
        </div>
        <el-table
          ref="table"
          :data="tableData"
          table-layout="auto"
          stripe
          border
          v-loading="loading"
          height="500"
          min-height="calc(100% - 85px)"
          max-height="calc(100% - 85px)"
          :header-cell-style="{ background: '#a6c3e2', color: '#fff' }"
        >
          <el-table-column
            prop="id"
            label="序号"
            min-width="80"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="englishName"
            label="英文名称"
            min-width="150"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="chineseName"
            label="中文名称"
            min-width="120"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="description"
            label="字段描述"
            min-width="250"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="unit"
            label="单位"
            min-width="80"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="typePrecision"
            label="类型与精度"
            min-width="100"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="rangeConstraint"
            label="值域/约束"
            min-width="220"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="referenceVariable"
            label="引用变量"
            min-width="100"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="typeCode"
            label="类别码"
            min-width="80"
            show-overflow-tooltip
          >
          </el-table-column>
          <!-- <el-table-column prop="address" label="表名"> </el-table-column> -->
        </el-table>
        <div class="block">
          <!--  @size-change="handleSizeChange" -->
          <el-button type="primary" style="height: 100%; width: 100px"
            >导入新标准<i class="el-icon-upload el-icon--right"></i
          ></el-button>
          <el-button
            type="primary"
            style="height: 100%; width: 100px"
            icon="el-icon-share"
            >导出新标准</el-button
          >
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  components: {},
  data() {
    return {
      categoryName: "", // 侧边栏搜索内容
      categoryList: [], // 侧边栏目录
      keyPath: [], // 侧边栏路径
      tableData: [], // 显示当前表数据
      tabName: "", // 表名
      categoryId: null,
      loading: false,
    };
  },
  created() {
    this.getCategoryList();
  },
  methods: {
    // 获取目录列表
    getCategoryList() {
      this.$axios({
        method: "get",
        url: "/pipeline_data_sys/category/list",
      })
        .then((res) => {
          this.categoryList = res.data;
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
    // 侧边栏搜索
    search() {
      // 为空提示
      if (this.categoryName == "") {
        this.$message({
          type: "warning",
          message: "请输入类别名",
          duration: 3000,
          showClose: true,
        });
      } else {
        // 请求路径
        this.$axios({
          url: `/pipeline_data_sys/category/${this.categoryName}/search`,
        })
          .then((res) => {
            // 路径为空，
            if (res.data.length == 0) {
              this.$message({
                type: "warning",
                message: "请输入正确的类别名",
                duration: 3000,
                showClose: true,
              });
            } else {
              this.keyPath = res.data;
              this.categoryId = this.keyPath[this.keyPath.length - 1];
              this.getSelect(this.categoryId);
            }
          })
          .catch((err) => {
            this.$message({
              type: "error",
              message: "网络出错了",
              duration: 3000,
              showClose: true,
            });
          });
      }
    },
    getSelect(key, keyPath) {
      this.categoryId = key;
      if (keyPath) {
        this.keyPath = keyPath;
      }
      // 拿categoryId请求表格数据
      console.log("open", key, keyPath);
      this.getTableData(this.categoryId);
    },
    // 请求表格数据
    getTableData(categoryId) {
      this.loading = true;
      this.$nextTick(() => {
        this.$refs.table.bodyWrapper.scrollTop = 0;
      });
      this.$axios({
        method: "get",
        url: `/pipeline_data_sys/attribute/${categoryId}/list`,
      })
        .then((res) => {
          if (res.data.attributes.length != 0) {
            this.tableData = res.data.attributes;
            this.tabName = this.tableData[0].tabName;
          } else {
            this.tableData = [];
            this.tabName = null;
          }
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        })
        .finally(() => {
          setTimeout(() => {
            this.loading = false;
          }, 400);
        });
    },
    handleCurrentChange(val) {
      this.loading = true;
      this.$nextTick(() => {
        this.$refs.table.bodyWrapper.scrollTop = 0;
      });
      this.$axios({
        url: `/pipeline_data_sys/attribute/${this.categoryId}`,
      })
        .then((res) => {
          setTimeout(() => {
            this.loading = false;
          }, 400);
          this.tableData = res.data.attributes;
        })
        .catch((err) => {
          this.$message({
            type: "error",
            message: "网络出错了",
            duration: 3000,
            showClose: true,
          });
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.home {
  height: calc(100% - 60px);
  .aside {
    height: 100%;
    overflow: hidden;
    border-right: solid 1px #e6e6e6;
    padding: 5px;
    background-color: #eef1f6;
    .search {
      display: flex;
      justify-content: space-around;
      margin: 10px 0;
      .ipt {
        width: 70%;
      }
      .btn {
        width: 25%;
      }
    }
    .menu {
      height: calc(100% - 110px);
      overflow: scroll;
      background-color: #fff;
      .el-menu {
        background-color: #fff;
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        -webkit-box-shadow: none;
        background: #e7e7e7;
        -webkit-box-shadow: none;
      }

      &::-webkit-scrollbar-track {
        -webkit-box-shadow: none;
        border-radius: 10px;
        background: #eef1f6;
        -webkit-box-shadow: none;
      }
    }
    .mapping {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 40px;
      margin: 10px 0px;
      background-color: #ecf5ff;
      border: 1px solid #b3d8ff;
      border-radius: 3px;
    }
  }
  .el-main {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    height: 100%;
    padding: 8px 5px;
    .tableHeader {
      height: 30px;
      margin-bottom: 3px;
      line-height: 30px;
      font-size: 18px;
    }
    /deep/ .el-table {
      width: 100%;
    }
    /deep/ .el-table__header {
      height: 80px;
      background-color: #e3e5e9;
    }

    .block {
      position: absolute;
      right: 50px;
      bottom: 10px;
      height: 40px;
    }
  }
}
/deep/ .el-table--scrollable-x .el-table__body-wrapper {
  overflow-x: auto;
}
/deep/ .el-table__body-wrapper::-webkit-scrollbar {
  width: 10px; /*滚动条宽度*/
  height: 10px; /*滚动条高度*/
}
// /deep/ .el-table__body-wrapper::-webkit-scrollbar-track {
//   border-radius: 10px; /*滚动条的背景区域的圆角*/
// }
// /deep/ .el-table__body-wrapper::-webkit-scrollbar-thumb {
//   border-radius: 10px; /*滚动条的圆角*/
// }
/deep/ .el-button {
  padding: 0;
}
/deep/ .el-menu {
  border-right: none;
}
/deep/ .el-submenu__title {
  text-align: left;
}
/deep/ .el-submenu .el-menu-item {
  text-align: left;
}
/deep/ .el-table .el-table__cell {
  padding: 5px 3px;
}
</style>
